<script setup lang="uts">
	import store from './store/index.js';
	import { onLaunch, onShow, onHide } from '@dcloudio/uni-app';
	
	let firstBackTime = 0
	
	// 应用启动时
	onLaunch(() => {
		uni.__f__('log','at App.vue:9','App Launch')
		// 确保在应用启动时初始化store
		// 将store挂载到全局
		if (uni && !uni.$store) {
			uni.$store = store;
		}
	})
	
	// 应用显示时
	onShow(() => {
		uni.__f__('log','at App.vue:19','App Show')
	})
	
	// 应用隐藏时
	onHide(() => {
		uni.__f__('log','at App.vue:24','App Hide')
	})
	
	// 安卓返回键处理
	const handleLastPageBackPress = () => {
		uni.__f__('log','at App.vue:29','App LastPageBackPress')
		if (firstBackTime == 0) {
			uni.showToast({
				title: '再按一次退出应用',
				position: 'bottom',
			})
			firstBackTime = Date.now()
			setTimeout(() => {
				firstBackTime = 0
			}, 2000)
		} else if (Date.now() - firstBackTime < 2000) {
			firstBackTime = Date.now()
			uni.exit()
		}
	}
	
	// 应用退出时
	const handleExit = () => {
		uni.__f__('log','at App.vue:47','App Exit')
	}
	
	// 将处理函数挂载到全局
	if (uni) {



		uni.$onExit = handleExit
	}
</script>

<style>
	/*每个页面公共css */
	.uni-row {
		flex-direction: row;
	}

	.uni-column {
		flex-direction: column;
	}
</style>